import React, {useState} from 'react';
import './App.css';
import MapChart from "./MapChart";
import { Tooltip as ReactTooltip } from "react-tooltip";

function App() {
    const [content, setContent] = useState("");
    return (
    <div className="App">
      <article>
          <h1 id="app-title" style={{ backgroundColor: "#999" }}>
              Hello Tooltip Example
          </h1>
          <h2 id="second-app-title" style={{ backgroundColor: "#999" }}>
              This is a basic example on how to use ReactTooltip
          </h2>
          <ReactTooltip
              anchorId="app-title"
              place="bottom"
              content="Hello world! I'm a Tooltip"
          />
          <ReactTooltip
              anchorId="second-app-title"
              place="bottom"
              variant="info"
              content="I'm a info tooltip"
          />
          <MapChart setTooltipContent={setContent} />
          <ReactTooltip place="bottom">{content}</ReactTooltip>
      </article>
    </div>
  );
}

export default App;
